import { race } from "racing-bars";

const chartOptions = {
    autoRun: false,
    labelsPosition: 'outside',
    labelsWidth: 62,
    showIcons: true,
    mouseControls: "all",
    tickDuration: 150,
    theme: 'dark'
};

const calculateChartHeight = (numCoins) => {
    return (300 +(numCoins * 20)) + 'px';
}

class RaceBarChart {
    private rootElement: HTMLElement;
    private overlayElement: HTMLElement;
    private playButtonElement: HTMLElement;
    private chartElement: HTMLElement;
    private selectedCoins: Set<string>;
    private coinsData: Map<string, any[]>;
    private selectedCoinsRows: any[];
    private chart: any;

    constructor(rootElement, selectedCoins, coinsData) {
        this.rootElement = rootElement;
        this.overlayElement = rootElement.querySelector('.chart-overlay');
        this.playButtonElement = rootElement.querySelector('.play-button');
        this.chartElement = rootElement.querySelector('.race');
        this.selectedCoins = selectedCoins;
        this.coinsData = coinsData;
        this.selectedCoinsRows = [];
        this.chart = null;

        this.initialize();
    }

    async initialize() {
        this.chart = await race(this.selectedCoinsRows, this.chartElement, chartOptions);
        this.playButtonElement.addEventListener('click', () => {
            this.hideOverlay();
            this.chart.play();
        });
    }

    showLoading() {
        this.overlayElement.classList.add('visible', 'loading');
    }

    showPlayButton() {
        this.overlayElement.classList.remove('loading');
        this.overlayElement.classList.add('visible');
    }

    hideOverlay() {
        this.overlayElement.classList.remove('visible', 'loading');
    }

    private updateSelectedCoinsRows() {
        this.selectedCoinsRows = Array.from(this.selectedCoins)
            .flatMap(coinId => this.coinsData.get(coinId) || []);
    }

    play() {
        this.chart.play();
    }

    pause() {
        this.chart.pause();
    }

    async updateChart(shouldShowPlay = true) {
        this.showLoading();
        
        // Ensure minimum loading time of 300ms
        await new Promise(resolve => setTimeout(resolve, 300));
        
        this.updateChartHeight(this.selectedCoins.size);
        this.updateSelectedCoinsRows();
        
        this.chartElement.innerHTML = '';
        this.chart = await race(this.selectedCoinsRows, this.chartElement, chartOptions);
        this.chart.pause();
        
        if (shouldShowPlay) {
            this.showPlayButton();
        } else {
            this.hideOverlay();
            this.chart.play();
        }
    }

    private updateChartHeight(numCoins) {
        this.chartElement.style.height = calculateChartHeight(numCoins);
    }
}

export default RaceBarChart;